.sub-content::before {
    position: absolute;
    content: "";
    background-image: url(../images/common/contents-logo-1.png);
    background-repeat: no-repeat;
    background-size: contain;
    display: block;
    height: 0;
    top: rem(-23);
    left:calc(50% - min(46vw,rem(350)));
    max-width: rem(120);
    width: calc(120 / 375 * 100%);
    padding-bottom: calc(55 / 375 * 100%);
    @include mq(md) {
        max-width: rem(300);
        width: calc(300 / 1130 * 100%);
        padding-bottom: calc(138 / 1130 * 100%);
        left:calc(50% - min(48vw,rem(620)));
        top: rem(-65);
    
    }
}
